import React, { useState } from "react";
import { Anchor, Heading, TextInput } from "grommet";
import { Facebook } from "grommet-icons";
import { assign, getQueryString } from "../helpers/index";
import Login from "../containers/Login";
import Register from "../containers/Login/Register";
import Parse, { fblogIn } from "../library/parse";
import Box from "../components/Box";
import Button from "../components/Button";
const Component = () => {
  const qs = getQueryString();
  const [model, setModel] = useState({});
  const [message, setMessage] = useState("");
  return (
    <Box margin="0">
      <Heading level={3}> Login</Heading>
      <Login />
      <Box gap="small" direction="row" wrap={true}>
        <Button
          onClick={() =>
            fblogIn((e) => {
              assign("/");
            }, "public_profile,email")
          }
          icon={<Facebook />}
          label="Login with Facebook"
          primary
        />

        {qs && qs.seller && (
          <Button
            onClick={() =>
              fblogIn((e) => {
                assign("/");
              }, "public_profile,email,pages_show_list,pages_read_engagement,pages_manage_posts")
            }
            icon={<Facebook />}
            label="Login with Facebook as Seller"
            primary
          />
        )}
      </Box>
      <Heading level={4}>Or Create An Account</Heading>
      <Register />
    </Box>
  );
};
export default Component;
